import gsap from '../../node_modules/gsap/all.js';
import { ScrollTrigger } from "../../node_modules/gsap/ScrollTrigger.js";
gsap.registerPlugin(ScrollTrigger);

// 
window.addEventListener("DOMContentLoaded", async () => {
    const stagger = 0.05;

    gsap.utils.toArray(".make").forEach((el) => {
      const q = gsap.utils.selector(el);

      gsap
        .timeline({
          scrollTrigger: {
            trigger: el,
            start: "top 100%",
            toggleActions: "play none none reset",
          },
        })
        .fromTo(
          // 
          q(".headline .rect"),
          {
            x: "-100%",
          },
          {
            x: "100%",
            duration: 1,
            stagger,
            ease: "power3.inout",
          },
        )
        .fromTo(
          q(".headline .label"),
          {
            alpha: 0,
          },
          {
            alpha: 1,
            duration: 0.3,
            delay: 0.5,
            stagger,
          },
          "<",
        )
        .fromTo(
          q(".slideX"),
          {
            alpha: 0,
            x: -32,
          },
          {
            alpha: 1,
            x: 0,
            duration: 0.75,
            delay: 0.2,
            stagger,
            ease: "power3.out",
          },
          "<",
        );
    });
  });

  gsap.from(".c-box", {rotation: 27,
    x: 800,//px
    stagger: 0.5,//
    backgroundColor: "blue",//
    repeat: -1,//-1
    repeatDelay: 5,//
    yoyo: true,//
    duration: 1//
  });

  // 
  function initTypeAnim() {
    ityped.init(document.querySelector(".ityped"), {
      strings: [""], // 
      startDelay: 0, //
      typeSpeed: 120, //
      loop: false, //
      backSpeed: 80, //
      backDelay: 150, //
      showCursor: true, //
      cursorChar: "|", //
      contentType: 'html'
    });
  }
  ScrollTrigger.create({
    trigger: ".ityped",
    start: "top 90%",
    onEnter: initTypeAnim,
    once: true,
  });

  gsap.from('.js-demo-right', { //
    x: 800, //800px
    stagger: 1,
    scrollTrigger: {
      trigger: '.js-trigger',//
      start: 'top center' //
    }
  });

  gsap.from('.material_right', { //
    x: 800, //800px
    stagger: 1,
    scrollTrigger: {
      trigger: '.js-demo',//
      start: 'top center' //
    }
  });

  // 

  ScrollTrigger.create({
    trigger: ".bottom",
    start: "top 50%",
    toggleClass: "is-crossed",
  });
  const bottomTl = gsap.timeline({
    scrollTrigger: {
      trigger: ".bottom",
      start: "top bottom",
      end: "center center",
      scrub: 1,
    },
  });
  bottomTl
    .fromTo(
      ".js-bottom_left",
      {
        xPercent: -100,
      },
      { xPercent: 0 }
    )
    .fromTo(
      ".js-bottom_right",
      {
        xPercent: 100,
      },
      { xPercent: 0 },
      "<"
    );

  // 

  gsap.config({
    nullTargetWarn: false,
  });


  const wrapper = document.querySelector('#wrapper');
  if(wrapper) {
      // gsap.registerPlugin(ScrollTrigger); // npm/yarn
      const panels = gsap.utils.toArray('.js-panel');
      const wrapperWidth = wrapper.offsetWidth;
      /**
      * 
      */
      gsap.to( panels, {
          xPercent: -100 * (panels.length - 1), // transformX
          ease: "none", // easing
          scrollTrigger: { // scrollTrigger
              trigger: wrapper, // 
              pin: true, // 
              scrub: 1, // 
              end: () => "+=" + wrapperWidth // 
          }
      })
  }

  var frame_count  = 154,
  offset_value = 170;

gsap.to(".p-bird__img", {
backgroundPosition: (-offset_value * frame_count) + "px 50%",
ease: "steps(" + frame_count + ")",
onComplete: () => {
  document.getElementById("img").classList.add("item");
},
scrollTrigger: {
  trigger: ".p-bird",
  start: "top top",
  end: "+=" + (frame_count * offset_value),
  pin: true,
  repeat: 1,
  scrub: true
}
});









